import Vue from 'vue';
import App from './App';

import VueRouter from 'vue-router';
import VueResource from 'vue-resource';

// 路由模块和http模块
Vue.use(VueResource);
Vue.use(VueRouter);

const router = new VueRouter();

router.map({
  '/index': {
    component: App,
  },
});

router.redirect({
  '*': '/index',
});
/* eslint-disable no-new */
router.start(App, '#app');



