<template >
  <div id="toolbar">
    <i class="glyphicon logo"><img src="../assets/logo.png" width="30" height="30" alt="" /></i>
    <i @click="newNote" class="glyphicon glyphicon-plus"></i>
    <i @click="toggleFavorite" class="glyphicon glyphicon-star" :class="{stared: activeNote.favorite}"></i>
    <i @click="deleteNote" class="glyphicon glyphicon-remove"></i>
  </div>
</template>

<script>
  import { newNote, deleteNote, toggleFavorite } from '../vuex/actions';
  import { activeNote } from '../vuex/getters';
  export default {
    vuex: {
      getters: {
        activeNote,
      },
      actions: {
        newNote,
        deleteNote,
        toggleFavorite,
      },
    },
  };
</script>

<style lang="scss" scoped>
  #toolbar {
    float: left;
    width: 80px;
    height: 100%;
    background-color: #304140;
    color: #767676;
    padding: 35px 25px 25px 25px;

    .stared {
      color: #f7ae4f;
    }

    i {
      font-size: 30px;
      margin-bottom: 35px;
      cursor: pointer;
      opacity: 0.8;
      transition: opacify 0.5s ease;

      &:hover {
        opacity: 1;
      }
    }
  }
</style>



